<template>
  <view class="search-box">
    <view class="search-input">
      <text class="iconfont icon-search"></text>
      <input type="text" placeholder="搜索目的地、景点、攻略" @input="onInput" />
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      searchText: ''
    }
  },
  methods: {
    onInput(e) {
      this.searchText = e.detail.value
      this.$emit('search', this.searchText)
    }
  }
}
</script>

